import React from "react"
const Person = ({personToshow,deleteHandler})=>{
    return (
    <div>
        <table>
         
        </table>
       
          
            
                                {personToshow.map((person)=>
                        
                        <tr className="persons" key={person.id}>
                        <td className="nambtn_area">{`${person.name} :`}</td>   
                        <td className="numbtn_area">{person.number}</td>
                         <td className="delbtn_area"> <button className="delBtn" type="submit" onClick={() => deleteHandler(person.id, person.name)}>
                    delete
                        </button></td>
                        </tr>
            
          
          
        
      
     
      
      )}
    </div>
   )

}
export default Person