import React,{useState,useEffect} from "react";
import logo from "./logo.png"
import Filter from "./Filter"
import personService from "./Service";
import Person from "./Person";
import "./index.css"

const App=()=>{
  const [person,setPerson]= useState([])
  
  const [newName,setNewname]=useState("")
  const [newNum,setNewnum]=useState("")
  const [filter, setFilter]= useState("")
  const [errorm,setError] = useState(null)
  const [success,setSuccess]=useState(null)
  

  useEffect(() => {
    personService.getAll().then((initialPerson) => {
      console.log("successfull");

      setPerson(initialPerson);
    });
  }, []);

  
  const submitEvent=(event)=>{
    event.preventDefault()
    const addObj={
      name:newName,
      number:newNum,
    }
    const existPerson= person.find((val)=>val.name.toLowerCase() === addObj.name.toLowerCase())
    const found= person.some(el=>el.name.toLowerCase() === addObj.name.toLowerCase())
    const changeNum={...existPerson,number:newNum};
      console.log(person.some(person=>person.name.toLowerCase()))
      console.log(addObj.name.toLowerCase())
      console.log(found)
    if(found === true){
      const confirmChange= window.confirm(`${addObj.name} is already added to phonebook ,replace the old number with a new one?`,console.log(existPerson)
     );
       if(confirmChange){
        personService.update(existPerson.id,changeNum)
        .then((updatePer)=>{
          setPerson(person.map((val)=>
            val.name === changeNum.name ? updatePer :val
          ))
          
          setSuccess(`${newName} is updated successfully`)
           setTimeout(()=>{
             setSuccess(null)
           },3000)
        }).catch((error)=>{
         setError(
           `Information of ${addObj.name} is already been removed from server!`
           
         )
           setTimeout(() => {
             setError(null);
           }, 5000)
       })
       }
       else{
        setNewname("")
        setNewnum("")
       
       }
     
     
    }
    else
    {
      personService.create(addObj)
      .then((response)=> { 
        setPerson(person.concat(response))
        
        console.log(setPerson)
        setSuccess(`${newName} is added to the phoonebook`)
        setTimeout(()=>{
          setSuccess(null)
        },3000)
       
      }).catch(error=>{
       
        console.log(error.response.data);
        console.log('typeof error.response.data.error')
        setError(`${error.response.data.error}`)
        // setNewname("")
        // setNewnum("")
          setTimeout(() => {
            setError(null);
          }, 5000)
      }
      )}
      setNewname("")
      setNewnum("")
      
    
  }
  const deleteHandler=(id,name)=>{
   if( window.confirm(`Delete ${name}?`)){
     console.log(`Delete the person of id ${id}`)
     const note=(person.filter((person)=>person.id !==id))
     console.log(note)
   
   personService
   .remove(id)
   .then((response)=>{
    setPerson(note)
    setSuccess(`${name} deleted`);
          setTimeout(() => {
            setSuccess(null);
          }, 5000);

   }).catch((error) => {
    
    setError(
      `the note '${note.name}' was already deleted from server`
    );
    setTimeout(() => {
      setError(null);
    }, 5000);
  })
  }
}
  const Error=({errorMsg})=>{
    if(errorMsg===null){
      return null
    }
    return <div className="error">{errorMsg}</div>
  }
  const Success =({successMSg})=>{
    if(successMSg=== null){
      return null
    }
    return <div className="success">{successMSg}</div>
  }
  const eventfilter = (event) => {
    setFilter(event.target.value);
  };
  const personToshow = filter.length>=0 ?  person.filter(person=>person.name.toLowerCase().includes(filter.toLowerCase())):person;
  return(
    <>
    <div className="container">
<div className="logo">
<a href ="#"><img src={logo} alt="logo"/></a>
</div>
<h1 >PhoneBook</h1>
</div>
    <Error errorMsg={errorm}/>
    <Success successMSg={success}/>
    <Filter filter={filter}  eventfilter={eventfilter} />
    <div className="sec_part">
    <Person personToshow={personToshow} 
    deleteHandler={deleteHandler} 
    />
    <form onSubmit={submitEvent}>
      <input className="input" placeholder ="new contact name.." value={newName} type="text" onChange={(event)=>setNewname(event.target.value)}/><br/>
      <input className="input" placeholder ="new contact number.." value={newNum} type="number" onChange={(event)=>setNewnum(event.target.value)}/>
      <button className ="save" className="save" type="submit">add</button>
    </form>
    </div>
    
    </>
  )
}
export default App
